$white:     #fff;
$gray-100:  #f8f9fa;
$gray-600:  #6c757d;
$gray-900:  #212529;
$black:     #000;
$blue:      #0d6efd;
$indigo:    #6610f2;
$purple:    #6f42c1;
$pink:      #d63384;
$red:       #dc3545;
$orange:    #fd7e14;
$yellow:    #ffc107;
$green:     #198754;
$teal:      #20c997;
$cyan:      #0dcaf0;
$primary:   $blue;
$secondary: $gray-600;
$success:   $green;
$info:      $cyan;
$warning:   $yellow;
$danger:    $red;
$light:     $gray-100;
$dark:      $gray-900;

$theme-colors: (
  "primary":   $primary,
  "secondary": $secondary,
  "success":   $success,
  "info":      $info,
  "warning":   $warning,
  "danger":    $danger,
  "light":     $light,
  "dark":      $dark,
  "blue":      $blue,
  "indigo":    $indigo,
  "purple":    $purple,
  "pink":      $pink,
  "red":       $red,
  "orange":    $orange,
  "yellow":    $yellow,
  "green":     $green,
  "teal":      $teal,
  "cyan":      $cyan,
);

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/mixins";
@import '~bootstrap/scss/variables';

.field {
    &:last-child {
        margin-bottom: 0 !important;
    }
    .slick-next::before, .slick-prev::before {
        color: $primary;
    }
    .slick-prev, .slick-next {
        z-index: 1;
    }
    .slick-prev {
        left: 10px;
    }
    .slick-next {
        right: 10px;
    }
    .slick-dots li {
        &.slick-active button::before, button::before {
            color: $primary;
        }
    }
    &[data-displayer=colour_default] {
        .colour {
            width: 40px;
            height: 40px;
            border-radius: 100%;
        }
    }
    &[data-displayer=matrix_slick], &[data-displayer=entry_slick], &[data-displayer=category_slick], &[data-displayer=user_slick], &[data-displayer=tag_slick] {
        & > .field-content {
            padding: 0 25px;
            .slick-prev {
                left: -25px;
            }
            .slick-next {
                right: -25px;
            }
        }
    }
}