.bg-header {
  background-color: var(--header-bg);
}

header {
  position: relative;
}

.block[data-handle=system-sitename] a {
  text-decoration: none;
  margin: 0;
}

@media all and (max-width: 992px) {
  .block[data-handle=bootstrap-main-menu] {
    position: absolute;
    right: 1rem;
    top: 1rem;
  }
  .block[data-handle=bootstrap-main-menu] .container-fluid {
    padding: 0;
  }

  .navbar-toggler {
    z-index: 10001;
  }

  #region-header-left {
    margin-bottom: 1rem;
  }

  #mainMenu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10000;
    background: var(--header-bg);
  }
  #mainMenu .navbar-nav {
    position: absolute;
    top: 1rem;
    left: 1rem;
  }
}